ul {
    display: inline-flex;
    text-align: center;
    justify-content: center;
    align-items: center;
  }
  li {
    font-family: 'Trebuchet MS', 'Lucida Sans';
    font-size: 30px;
    font-weight: 900;
    padding: 7px 20px;
    margin: 10px;
    border-radius: 5px;
    border-left: 10px solid #f05d22;
    box-shadow: 2px -2px 5px 0 rgba(0, 0, 0, 0.1),
      -2px -2px 5px 0 rgba(0, 0, 0, 0.1), 2px 2px 5px 0 rgba(0, 0, 0, 0.1),
      -2px 2px 5px 0 rgba(0, 0, 0, 0.1);
    font-size: 20px;
    letter-spacing: 2px;
    transition: 0.3s all linear;
  }
  li:nth-child(1) {
    border-color: #f05d22;
  }
  li:nth-child(2) {
    border-color: #8bc63e;
  }
  li:nth-child(3) {
    border-color: #fcba30;
  }
  li:nth-child(4) {
    border-color: #1ccfc9;
  }
  li:nth-child(5) {
    border-color: #493224;
  }
  li {
    border-left: 10px solid transparent;
  }
  li:nth-child(1) {
    border-right: 10px solid #f05d22;
  }
  li:nth-child(2) {
    border-right: 10px solid #8bc63e;
  }
  li:nth-child(3) {
    border-right: 10px solid #fcba30;
  }
  li:nth-child(4) {
    border-right: 10px solid #1ccfc9;
  }
  li:nth-child(5) {
    border-right: 10px solid #493224;
  }